ransform .2s ease-out;\r\n}\r\n.hex h1 {\r\n    bottom: 50%;\r\n    padding-top: 50%;\r\n    font-size: 1.5em;\r\n    z-index: 1;\r\n    -webkit-transform: translateY(-100%) translatez(-1px);\r\n            transform: translateY(-100%) translatez(-1px);\r\n}\r\n.hex h1:after {\r\n    content: '';\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 45%;\r\n    width: 10%;\r\n    text-align: center;\r\n    border-bottom: 1px solid #fff;\r\n}\r\n.hex p {\r\n    top: 50%;\r\n    padding-bottom: 50%;\r\n    -webkit-transform: translateY(100%) translatez(-1px);\r\n            transform: translateY(100%) translatez(-1px);\r\n}\r\n/*** HOVER EFFECT  **********************************************************************/\r\n.hexIn:hover h1, .hexIn:focus h1, .hexIn:hover p, .hexIn:focus p {\r\n    -webkit-transform: translateY(0%) translatez(-1px);\r\n            transform: translateY(0%) translatez(-1px);\r\n}\r\n/*** SPACING AND SIZING *****************************************************************/\r\n@media (min-width:1201px) {  /* <- 2-3  hexagons per row */\r\n.hex {\r\n    width: 32.666%;    /* = (100-2) / 3 */\r\n    padding-bottom: 37.720%;    /* =  width / sin(60) */\r\n}\r\n.hex:nth-child(5n+1),\r\n.hex:nth-child(5n+2) {\r\n    -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);\r\n            transform: translateX(50%) rotate(-60deg) skewY(30deg);\r\n}\r\n.hex:nth-child(5n+3),\r\n.hex:nth-child(5n+4),\r\n.hex:nth-child(5n+5) {\r\n    margin-top: -8.564%;\r\n    margin-bottom: -8.564%;\r\n}\r\n.hex:nth-child(5n+2),\r\n.hex:nth-child(5n+4) {\r\n    margin-right: 1%;\r\n    margin-left: 1%;\r\n}\r\n.hex:nth-child(5n+1) {\r\n    margin-left: 0.5%;\r\n}\r\n.hex:nth-child(5n+3),\r\n.hex:nth-child(5n+6) {\r\n    clear: left;\r\n}\r\n}\r\n@media (max-width: 1200px) {  /* <- 1-2  hexagons per row */\r\n.hex {\r\n    width: 49.5%;    /* = (100-1) / 2 */\r\n    padding-bottom: 57.158%;    /* =  width / sin(60) */\r\n}\r\n.hex:nth-child(3n+1) {\r\n    -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);\r\n            transform: translateX(50%) rotate(-60deg) skewY(30deg);\r\n}\r\n.hex:nth-child(3n+2),\r\n.hex:nth-child(3n+3) {\r\n    margin-top: -13.423%;\r\n    margin-bottom: -13.423%;\r\n}\r\n.hex:nth-child(3n+1) {\r\n    margin-left: 0.5%\r\n}\r\n.hex:nth-child(3n+3) {\r\n    margin-left: 1%;\r\n}\r\n.hex:nth-child(3n+2),\r\n.hex:nth-child(3n+4) {\r\n    clear: left;\r\n}\r\n}\r\n@media (max-width: 400px) {\r\n#hexGrid {\r\n    font-size: 13px;\r\n}\r\n}\r\n.switch {\r\n    position: relative;\r\n    display: inline-block;\r\n    width: 40px;\r\n    height: 24px;\r\n}\r\n.switch input { \r\n    opacity: 0;\r\n    width: 0;\r\n    height: 0;\r\n}\r\n.slider {\r\n    position: absolute;\r\n    cursor: pointer;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background-color: #ccc;\r\n    -webkit-transition: .4s;\r\n    transition: .4s;\r\n}\r\n.slider:before {\r\n    position: absolute;\r\n    content: \"\";\r\n    height: 18px;\r\n    width: 18px;\r\n    left: 4px;\r\n    bottom: 3px;\r\n    background-color: white;\r\n    -webkit-transition: .4s;\r\n    transition: .4s;\r\n}\r\ninput:checked + .slider {\r\n    background-color: #2196F3;\r\n}\r\ninput:focus + .slider {\r\n    box-shadow: 0 0 1px #2196F3;\r\n}\r\ninput:checked + .slider:before {\r\n    -webkit-transform: translateX(14px);\r\n    transform: translateX(14px);\r\n}\r\n/* Rounded sliders */\r\n.slider.round {\r\n    border-radius: 34px;\r\n}\r\n.slider.round:before {\r\n    border-radius: 50%;\r\n}\r\n  \r\n/*# sourceMappingURL=data:application/json;base64, */"